<template>
  <div class="about">
    <!-- <NavBar :channelName="channel.names" :photo="channel.photo" /> -->
    <v-container fluid >
      <v-row>
        <v-col cols="12" lg="6" style="background: transparent"> 
          <template v-if="serverSelect.length > 0">
            <v-img
                alt="Vuetify Logo"
                class="shrink mr-2"
                contain
                :src="serverSelect[0].icon"
                transition="scale-transition"
                width="150"
                style="margin-left:auto;margin-right:auto !important"
              />
              <h1 class="text-center wordServer">💙 {{serverSelect[0].name}} 💙</h1>
              <h3 class="text-center wordServer">🎮 {{serverSelect[0].players}} 🎮</h3>
          </template>
        </v-col>
        <v-col cols="12" lg="6">
          <v-data-table
            v-model="serverSelect"
            show-select
            hide-default-footer
            :single-select="true"
            :headers="headers"
            :items="serversList"
            item-key="name"
            multi-sort
            class="elevation-1"
          >
            <template v-slot:item.online="{ item }">
              <v-chip :color="getColor(item.online)" dark>
                <template v-if="item.online">😎</template>
                <template v-else>🙂</template>
              </v-chip>
            </template>
          </v-data-table>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
// @ is an alias to /src
import NavBar from "@/components/NavBar.vue";

export default {
  name: "Servers",
  components: {
    NavBar,
  },
  data: () => ({
    channel: {},
    servers: {},
    serverSelect: [],
    headers: [
      {
        text: "Nombre",
        align: "start",
        value: "name",
      },
      { text: "IP", value: "ip", sortable: false },
      { text: "Jugadores", value: "players", sortable: false },
      { text: "Online", value: "online", sortable: false },
    ],
    serversList: [],
  }),
  methods: {
    getMyChannel() {
      var api = this.$store.state.URL + "/channels/myChannels";
      this.axios
        .get(api)
        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data;
        })
        .then((data) => {
          this.channel = data.myChannel;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    getColor(online) {
      if (online) return "green";
      return "red";
    },
    getMyServerMC() {
      var api = this.$store.state.URL + "/servers/myServerMCRange";
      this.axios
        .get(api)
        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data;
        })
        .then((data) => {
          this.servers = data.servers;
          for (var i = 0; i < this.servers.length; i++) {
            var server = {};
            server.ip = this.servers[i].ip;
            server.players =
              this.servers[i].players.online +
              " / " +
              this.servers[i].players.max;
            server.icon = this.servers[i].icon;
            server.name = this.servers[i].nameServer;
            server.online = this.servers[i].online;

            this.serversList.push(server);
          }
        })
        .catch((e) => {
          console.log(e);
        });
    },
    getServerData(data){
      // console.log(data)
    }
  },
  mounted() {
    this.getMyChannel();
    this.getMyServerMC();
  },
};
</script>
